import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EditorComponent } from './edk/editor/editor.component';
import { EDKComponent } from './edk/edk.component';
import { RendererComponent } from './edk/renderer/renderer.component';
import { LabelerComponent } from './edk/labeler/labeler.component';

const routes: Routes = [
  {path: 'edk', component: EDKComponent},
  {path: 'labeler', component: LabelerComponent},
  {path: 'editor', component: EditorComponent},
  {path: 'renderer', component: RendererComponent},
  {path: '', redirectTo: '/renderer', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
