import { AfterViewInit, Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { DragControls } from 'three/examples/jsm/controls/DragControls.js';
import * as three from 'three';


@Component({
  selector: 'app-labeler',
  templateUrl: './labeler.component.html',
  styleUrls: ['./labeler.component.scss']
})
export class LabelerComponent implements OnInit, AfterViewInit {

  constructor() {
    console.log(this.videoContainer);
    const width = window.innerWidth;
    const height = window.innerHeight - 64;
    this.renderer = new three.WebGLRenderer({ antialias: true });
    this.scene = new three.Scene();
    this.camera = new three.PerspectiveCamera(25, width / height, 0.1, 1000);
    this.orbitControls = new OrbitControls(this.camera, this.renderer.domElement);
    const geometry = new three.SphereBufferGeometry(0.1, 32, 32);
    const material = new three.MeshBasicMaterial({ color: 0xafaab9 });
    const dot = new three.Mesh(geometry, material);
    dot.position.x = 0;
    dot.position.z = 3;
    dot.position.y = 0;
    this.scene.add(dot);
    this.dragControls = new DragControls([dot], this.camera, this.renderer.domElement);
    this.dragControls.addEventListener('drag', () => this.renderer.render(this.scene, this.camera));
    this.orbitControls.update();
  }

  // controlling
  orbitControls: OrbitControls;
  dragControls: DragControls;

  // necessary elements for rendering
  @ViewChild('labelerContainer') rendererContainer!: ElementRef;
  @ViewChild('video') videoContainer!: ElementRef;
  renderer: three.WebGLRenderer;
  scene: three.Scene;
  camera: three.PerspectiveCamera;
  mouse = new three.Vector2();
  raycaster = new three.Raycaster();

  // event listeners
  @HostListener('window:resize', ['$event'])
  onWindowResize(event: UIEvent): void {
    const width = window.innerWidth;
    const height = window.innerHeight - 64;
    this.renderer.setSize(width, height);
    this.camera.aspect = width / height;
    this.camera.updateProjectionMatrix();
  }

  @HostListener('document:mousemove', ['$event'])
  onDocumentMouseMove(event: MouseEvent): void {
    const rect = this.renderer.domElement.getBoundingClientRect();
    this.mouse.x = ((event.clientX - rect.left) / (rect.right - rect.left)) * 2 - 1;
    this.mouse.y = - ((event.clientY - rect.top) / (rect.bottom - rect.top)) * 2 + 1;
  }

  @HostListener('document:mouseclick', ['$event'])
  onDocumentMouseClick(event: MouseEvent): void {
    event.preventDefault();
    const draggableObjects = this.dragControls.getObjects();
    draggableObjects.length = 0;

    this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
    this.mouse.y = - (event.clientY / window.innerHeight) * 2 + 1;
    this.raycaster.setFromCamera(this.mouse, this.camera);
    const intersections = this.raycaster.intersectObjects(this.scene.children, true);
    if (intersections.length > 0) {
    }
  }

  @HostListener('document:keypress', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent): void {
    if (event.key === 'c' && event.ctrlKey) {
      this.switchControls();
    }
  }



  ngAfterViewInit(): void {
    this.camera.lookAt(this.scene.position);
    this.rendererContainer.nativeElement.appendChild(this.renderer.domElement);
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(window.innerWidth, (window.innerHeight - 64));
    this.initVideo();
    window.requestAnimationFrame(() => this.animate());
  }

  animate(): void {
    this.selectObject();
    window.requestAnimationFrame(() => this.animate());
    this.renderer.render(this.scene, this.camera);
  }

  ngOnInit(): void {
    this.initScene();

  }
  initVideo(): void {
    this.videoContainer.nativeElement.play();
    this.videoContainer.nativeElement.addEventListener('play', (): void => {
        console.log('playing');
      });
    const texture = new three.VideoTexture(this.videoContainer.nativeElement);
    texture.needsUpdate = true;
    const geometry = new three.BoxGeometry(16, 9, 0);
    const material = new three.MeshLambertMaterial({ color: 0xffffff, map: texture });
    const mesh = new three.Mesh(geometry, material);
    mesh.position.x = 0;
    mesh.position.y = 0;
    mesh.position.z = 0;
    this.scene.add(mesh);
  }

  initScene(): void {
    this.renderer.setClearColor(0xffffff);
    this.camera.position.set(20, 20, 20);
    this.orbitControls.enablePan = true;
    this.orbitControls.enableZoom = true;
    this.orbitControls.enableKeys = true;
    this.orbitControls.enabled = false;
    this.dragControls.enabled = true;

    const axesHelper = new three.AxesHelper(5);
    this.scene.add(axesHelper);

    const ambientLight = new three.AmbientLight(0xffffff);
    ambientLight.intensity = 2;
    this.scene.add(ambientLight);
  }

  selectObject(): void {
    this.raycaster.setFromCamera(this.mouse, this.camera);
    const mouseIntersection = this.raycaster.intersectObjects(this.scene.children);
    let go = '';
    for (const intersected of mouseIntersection) {
      if (intersected.object instanceof three.Mesh) {
        go = intersected.object.name;
      }
    }
  }

  switchControls(): void {
    if (this.orbitControls.enabled) {
      this.orbitControls.enabled = false;
      this.dragControls.enabled = true;
    } else {
      this.orbitControls.enabled = true;
      this.dragControls.enabled = false;
    }
  }

}



